.wrapper {
  display: flex;
  max-width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;

  .discountInfo {
    display: flex;
    margin-top: 1rem;
    .error {
      color: red;
      font-weight: bold;
      font-size: 1.2rem;
      text-align: center;
      margin: 1rem 0;
    }
    &__usedCode {
      display: flex;
      align-items: center;
      color: red;
      font-weight: bold;
      font-size: 1.2rem;
      margin-right: 20px;
    }
    &__button {
      font-weight: normal;
      padding: 0.2rem 0.2rem;
      font-size: 0.6rem;
      margin-left: 10px;
    }
  }

  .discount {
    display: flex;
    flex-direction: row;
    align-items: center;
    &__label {
      margin-right: 10px;
      text-transform: uppercase;
    }
    &__input {
      margin-right: 10px;
    }
    &__button {
    }
  }
}
